.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 10px;
}

.messages {
    padding: 10px;
}

.link {
    text-decoration: none;
    color: black;
}

a.active .dialog {
    font-weight: bold;
}

a.active .dialog img {
    width: 30px;
    height: 30px;
}

.dialog img {
    width: 25px;
    height: 25px;
    border-radius: 50%;
}

.dialog {
    padding: 7px 0;
    display: flex;
}

.dialog:hover {
    background: lightskyblue;
}

.sendButton button {
    background: whitesmoke;
}

.sendButton button:hover {
    background: lightskyblue;
}